<template>
  <div>
    <!-- <h3 class="title_container">内部记账依据汇总表</h3> -->
    <div class="search_container">
      <el-form :inline="true" ref="TableDataRef" :model="params" class="demo-form-inline">
        <el-form-item label="财务组织" size="mini">
          <el-select clearable v-model="params.company_id" multiple collapse-tags placeholder="请选择" @change="companyChange" @focus="getCompanyFoucs" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="创建人" size="mini">
          <el-select v-model="params.add_user_id" filterable @focus="getBusinessListFocus" multiple collapse-tags>
            <el-option :label="item.realname" :value="item.user_id" :key="item.user_id" v-for="item in addUserList.options"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="业务日期" size="mini">
          <el-date-picker v-model="timeValue1" @change="change" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
          </el-date-picker>
        </el-form-item>

        <el-form-item label="审核日期" size="mini">
          <el-date-picker v-model="timeValue" @change="change" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="结算方式" size="mini">
          <el-select clearable v-model="params.receivables_mode_id" @focus="getPayment">
            <el-option v-for="item in payment.options" :label="item.receivables_mode_name" :value="item.receivables_mode_id" :key="item.receivables_mode_id">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item size="mini" class="search-style search">
          <el-button type="primary" @click="onSearch('TableDataRef')" size="mini">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>

      </el-form>
    </div>
    <div class="add_delet" v-if="authority.b_export==1 && listShow">
      <el-dropdown placement="bottom-start">
        <span split-button style="cursor:pointer;color:#4e90e2">
          <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
        </span>
        <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
          <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
          <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <!-- <div class="add_delet">
      <el-select v-model="params.export_type" placeholder="请选择">
        <el-option label="CSV逗号分隔" value="2">CSV逗号分隔</el-option>
        <el-option label="Excel文件" value="1">Excel文件</el-option>
      </el-select>
      <el-button plain size="mini" @click="exportOrder" icon="iconfont icon-daochu10" class="exporT" v-if="authority.b_export==1">导出</el-button>
    </div> -->

    <div class="table_container" v-if="listShow">
      <!-- <h2 class="table_container_title">内部记账依据汇总表</h2> -->

      <table cellspacing="0">
        <thead>
          <tr>
            <th style="text-align:left;">结算方式</th>
            <th style="text-align:left;min-width:120px">开户银行</th>
            <th style="text-align:left;">银行账号后四位</th>
            <th align="right" v-for="item in TableData.company_list" :key="item.id">{{item.company_name}}</th>
            <th>合计</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item,index1) in TableData.tableData" :key="index1">
            <td v-if="item.receivables_mode_namedis!=true" :rowspan="item.receivables_mode_namespan">{{ item.receivables_mode_name }}</td>
            <td>{{ item.bank_name }}</td>
            <td>{{item.bank_number}}</td>
            <td align="right" v-for="(item2,index2) in item.list" :key="index2">{{item2 | priceFormatNone}}</td>
            <td align="right">{{item.sum | priceFormatNone}}</td>
          </tr>
          <tr style="background:#f8f9fa">
            <td colspan="3" style="font-weight:700;">合计</td>
            <td align="right" style="font-weight:700;" v-for="(item2,index3) in TableData.tableData.totalNum" :key="index3">{{item2 | priceFormatNone}}
            </td>
            <td align="right" style="font-weight:700;">{{TableData.tableData.totalSum | priceFormatNone}}</td>
          </tr>
        </tbody>
      </table>
      <el-row type="flex" class="row-bg">
        <el-col :span="12">
          <div>生成时间：{{allList.add_time}}</div>
        </el-col>
        <el-col :span="12">
          <div class="textAr">制表人：{{allList.add_user_name}}</div>
        </el-col>
      </el-row>
      <el-row type="flex" class="row-bg">
        <el-col :span="12">
          <div class="report_row">业务日期起始:{{params.business_start_time}}</div>
        </el-col>
        <el-col :span="12">
          <div class="textAr">业务日期起始:{{params.business_end_time}}</div>
        </el-col>
      </el-row>
    </div>

  </div>
</template>
<script>
import {
  getData,
  getDepartmentListCommon,
  getInventoryFinanceCompanyOpen
} from "common/utils/public_function/commonUtils";
export default {
  name: "reportReceiptsumrpt",
  data() {
    return {
      authority: {},
      params: {},
      addUserList: {
        options: []
      },
      searchParams: {},
      listShow: false,
      timeValue: [],
      timeValue1: [],
      exportType: null,
      warehouseList: {
        options: []
      }, //仓库管理
      allList: {},
      departmentList: {
        options: []
      },
      employeeList: {
        options: []
      },
      companyList: [],
      loading: true,
      company: "",
      TableData: {
        tableData: [],
        company_list: []
      },
      tableCompanyList: [],
      payment: {
        options: []
      }
    };
  },

  methods: {
    getCompanyFoucs() {
      getInventoryFinanceCompanyOpen(this, {
        type: 2
      });
    },
    getBusinessListFocus() {
      this.$common.options("getReceiptListXl", this.addUserList, {
        company_id: this.params.company_id
      }); //创建人
    },
    companyChange() {
      this.$set(this.params, "add_user_id", []);
      this.$set(this.params, "receivables_mode_id", null);
    },
    change(val) {
      //选择的日期
      this.params.start_time = val[0];
      this.params.end_time = val[1];
    },
    onReset() {
      this.$set(this.params, "company_id", []);
      this.$set(this.params, "add_user_id", []);
      this.$set(this.params, "receivables_mode_id", null);

      this.timeValue = [];
      this.timeValue1 = [];
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue1 && this.timeValue1.length == 2) {
        this.params.business_start_time = this.timeValue1[0];
        this.params.business_end_time = this.timeValue1[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      let params = Object.assign({}, this.params);
      params.company_id = this.params.company_id.join(",");
      params.add_user_id = this.params.add_user_id.join(",");
      let vm = this;
      this.$axios({
        method: "get",
        url: this.$api.getReceiptCommonList,
        params: params
      }).then(({ data }) => {
        if (data.code == 200) {
          vm.allList = data.data.desc;
          vm.TableData.tableData = data.data.list;
          vm.TableData.company_list = data.data.company_list;
          let ss = [];
          var result = data.data.list.map(item => {
            var res = Object.assign({}, item, {
              receivables_name: item.receivables_name
            });
            delete res.data;
            return [...item.data];
          });
          result.forEach(item => {
            ss.push(...item);
          });

          var a = this.$publicFution.combineCell(ss);
          a.totalNum = {};
          a.totalSum = 0;
          a.forEach(obj => {
            var newObj = {};
            for (const key in obj) {
              if (!Object.is(Number(key), NaN)) {
                newObj[key] = obj[key];
              }
            }
            obj.list = newObj;
            for (const key2 in obj.list) {
              a.totalNum[String(key2)] = 0;
            }
          });

          a.forEach(obj => {
            for (const key in obj.list) {
              a.totalNum[String(key)] += Number(obj.list[key]);
            }
            a.totalSum += obj.sum;
          });

          vm.TableData.tableData = a;
        }
      });
    },
    exportOrder(scheduleType) {
      // 导出采购信息汇总表
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });

      let params = Object.assign({}, this.params);
      params.company_id = this.params.company_id.join(",");
      params.add_user_id = this.params.add_user_id.join(",");
      this.$axMethod.exportReceiptCommonUrl(params).then(res => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    onSearch() {
      //查询
      this.listShow = true;
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue1) {
        this.params.business_start_time = this.timeValue1[0];
        this.params.business_end_time = this.timeValue1[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      let params = Object.assign({}, this.params);
      params.company_id = this.params.company_id.join(",");
      params.add_user_id = this.params.add_user_id.join(",");
      let vm = this;
      this.$axios({
        method: "get",
        url: this.$api.getReceiptCommonList,
        params: params
      }).then(({ data }) => {
        if (data.code == 200) {
          vm.allList = data.data.desc;
          vm.TableData.tableData = data.data.list;
          vm.TableData.company_list = data.data.company_list;
          let ss = [];
          var result = data.data.list.map(item => {
            var res = Object.assign({}, item, {
              receivables_name: item.receivables_name
            });
            delete res.data;
            return [...item.data];
          });
          result.forEach(item => {
            ss.push(...item);
          });

          var a = this.$publicFution.combineCell(ss);
          a.totalNum = {};
          a.totalSum = 0;
          a.forEach(obj => {
            var newObj = {};
            for (const key in obj) {
              if (!Object.is(Number(key), NaN)) {
                newObj[key] = obj[key];
              }
            }
            obj.list = newObj;
            for (const key2 in obj.list) {
              a.totalNum[String(key2)] = 0;
            }
          });

          a.forEach(obj => {
            for (const key in obj.list) {
              a.totalNum[String(key)] += Number(obj.list[key]);
            }
            a.totalSum += obj.sum;
          });

          vm.TableData.tableData = a;
        }
      });
    },
    getPayment() {
      if (this.params.company_id.length == 0) {
        this.payment.options = [];
      } else {
        this.$common.options("getCollectionListOption", this.payment, {
          company_id: this.params.company_id
        }); //回款类
      }
    }
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
  }
};
</script>

